.body-container {
  padding: 30px;
}

.body-container-calculator {
  padding: 30px;
  display: flex;
}

.main-header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  box-shadow: 0 4px 2px -2px rgb(214, 214, 214);
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 100;
}

.header-ul {
  display: flex;
}

.header-li {
  list-style-type: none;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
  align-items: center;
  margin: 0 10px 0 10px;
}

.header-li-item {
  font-weight: 700;
  text-decoration: none;
  color: black;
}

.logo {
  padding-right: 50px;
}

.calc-text-body {
  width: 320px;
}
